import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Pagination } from 'swiper/modules';
import { useLanguage } from '../../context/LanguageContext';
import { getContent } from '../../content/content';
import 'swiper/css';
import 'swiper/css/pagination';
import './TestimonialsSection.css';

interface Testimonial {
  quote: string;
  name: string;
  role: string;
  avatar: string;
}

interface TestimonialsSectionProps {
  testimonials?: Testimonial[];
}

const TestimonialsSection: React.FC<TestimonialsSectionProps> = ({ testimonials = [] }) => {
  const { lang } = useLanguage();
  const content = getContent(lang).pages.landing.section.eventContent;
  return (
    <section className="testimonials-section">
      <div className="testimonials-container">
        <div className="testimonials-header">
          <h2>{content.testimonials.heading}</h2>
          <p>{content.testimonials.subheading}</p>
        </div>
        
        <div className="testimonials-slider">
          <Swiper
            modules={[Autoplay, Pagination]}
            spaceBetween={30}
            slidesPerView={1}
            autoplay={{
              delay: 5000,
              disableOnInteraction: false,
            }}
            pagination={{
              clickable: true,
              bulletClass: 'testimonial-bullet',
              bulletActiveClass: 'testimonial-bullet-active',
            }}
            loop={true}
            className="testimonials-swiper"
          >
            {(testimonials.length > 0 ? testimonials : content.testimonials.list.map((testimonial: any, index: number) => ({
              ...testimonial,
              avatar: `https://images.unsplash.com/photo-${index % 2 === 0 ? '1494790108755-2616b612b786' : '1507003211169-0a1dd7228f2d'}?w=150&h=150&fit=crop&crop=face`
            }))).map((testimonial, index) => (
              <SwiperSlide key={index}>
                <div className="testimonial-card">
                  <div className="quote-section">
                    <div className="quote-icon">❝</div>
                    <p className="testimonial-quote">{testimonial.quote}</p>
                    <div className="quote-icon-end">❞</div>
                  </div>
                  <div className="testimonial-footer">
                    <div className="testimonial-author">
                      <img 
                        src={testimonial.avatar} 
                        alt={testimonial.name}
                        className="author-avatar"
                      />
                      <div className="author-info">
                        <h4 className="author-name">{testimonial.name}</h4>
                        <p className="author-role">{testimonial.role}</p>
                      </div>
                    </div>
                  </div>
                </div>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </div>
    </section>
  );
};

export default TestimonialsSection;