import React from 'react';
import { useLanguage } from '../../context/LanguageContext';
import { getContent } from '../../content/content';
import './EventSpeakers.css';

interface Speaker {
  name: string;
  title: string;
  description: string;
  image: string;
}

interface EventSpeakersProps {
  speakers?: Speaker[];
}

const EventSpeakers: React.FC<EventSpeakersProps> = ({ speakers = [] }) => {
  const { lang } = useLanguage();
  const content = getContent(lang).pages.landing.section.eventContent;
  
  return (
    <section id="speakers" className="speakers-section">
      <div className="speakers-container">
        <div className="speakers-header">
          <h2><span className="gold-text">{content.speakers.heading}</span> / {content.speakers.subheading}</h2>
        </div>
        
        <div className="speakers-grid">
          {(speakers.length > 0 ? speakers : content.speakers.list.map((speaker: any, index: number) => ({
            ...speaker,
            image: `https://randomuser.me/api/portraits/${index % 2 === 0 ? 'men' : 'women'}/${(index % 50) + 1}.jpg`
          }))).map((speaker: Speaker, index: number) => (
            <div key={index} className="speaker-card">
              <div className="speaker-image">
                <img src={speaker.image} alt={speaker.name} />
              </div>
              <div className="speaker-info">
                <h3 className="speaker-name">{speaker.name}</h3>
                <p className="speaker-title">{speaker.title}</p>
                <p className="speaker-description">{speaker.description}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default EventSpeakers;